@import '../../style/variable';

.modular_label {
    background-color: $bg_label;
    padding: 7px 0 7px 0;
    display: flex;
    justify-content: center;
    position: relative;

    .title {
        font-family: $os_regular;
        color: $text_70;
        font-size: 15px;
        margin-left: 10px; }
    .close {
        width: 8px;
        position: absolute;
        top: 50%;
        right: 12px;
        transform: translateY(-50%);
        cursor: pointer; } }




